.treatment__item {
  height: 600px;
  border-radius: 30px;
  overflow: hidden;
  /*     4    gap */
  flex-basis: 326.25px;
  flex-shrink: 1;
  transition: 0.2s ease;
  position: relative;
  display: flex;
}

.treatment__item p {
  color: #FFFFFF;
}

.treatment__item picture {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 100%;
  width: 100%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.treatment__item picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.treatment__text {
  position: relative;
  z-index: 5;
  margin-top: auto;
  padding: 30px;
}

.treatment__item:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(184.4deg, rgba(0, 0, 0, 0) 3.58%, #000000 143.72%);
  opacity: 1;
  z-index: 2;
  transition: opacity 0.2s ease;
}

.treatment__item:after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #5BC5B1;
  opacity: 0;
  z-index: 2;
  transition: opacity 0.2s ease;
}

@media (hover: hover) {
  .treatment__item:hover {
    flex-basis: 440px;
    flex-shrink: 0;
  }
  .treatment__item:hover:before {
    opacity: 0;
  }
  .treatment__item:hover:after {
    opacity: 1;
  }
}
.treatment__text {
  display: flex;
  flex-direction: column;
}

.treatment__title {
  font-size: 28px;
  line-height: 1.2;
  transition: font-size 0.2s ease;
