  padding: 30px;
  border-radius: 30px;
  box-shadow: 0px 5px 30px 0px rgba(0, 0, 0, 0.1019607843);
}

.navigation__title {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}

.navigation__title p {
  font-size: 28px;
  font-weight: 700;
  color: #3A3A3A;
}

.navigation__title span {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #2684FC;
  position: relative;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.navigation__title span.active {
  background-color: rgba(38, 132, 252, 0.5019607843);
}

.navigation__title span:before {
  content: "";
  display: block;
  width: 18px;
  height: 2px;
  border-radius: 2px;
  background-color: #FFFFFF;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navigation__title span:after {
  content: "";
  display: block;
  width: 2px;
  height: 18px;
  border-radius: 2px;
  background-color: #FFFFFF;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: height 0.2s ease, opacity 0.2s ease;
}

.navigation__title span.active:after {
  height: 0px;
  opacity: 0;
}

.navigation__block, .hide-item__content {
  max-height: 0px;
  overflow: hidden;
  transition: max-height 0.2s ease;
}

.navigation__items {
  margin-top: 20px;
  border-top: 1px solid #D8D8D8;
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  counter-reset: nav;
}

.navigation__item {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  padding-left: 35px;
  position: relative;
}

