  font-size: 18px;
  font-weight: 700;
  color: #4A4A4A;
  max-width: 80%;
  width: fit-content;
  transition: color 0.2s ease;
  counter-increment: nav;
}

.navigation__item > a.link:hover {
  color: #2684FC;
}

.navigation__item > a.link:before {
  content: counter(nav) ".";
  font-size: 18px;
  font-weight: 700;
  color: #4A4A4A;
  position: absolute;
  left: 0;
  top: 0;
  transition: color 0.2s ease;
}

.navigation__item > a.link:hover:before {
  color: #2684FC;
}

.navigation__item > button {
  margin-left: auto;
  margin-bottom: auto;
}

.navigation__sublink-wrapper {
  width: 100%;
}

