  display: flex;
  flex-direction: column;
  gap: 15px;
}

.navigation__subsublink > a.link {
  font-size: 16px;
  color: #4A4A4A;
  transition: color 0.2s ease;
  padding-left: 50px;
  position: relative;
}

.navigation__subsublink > a.link:hover {
  color: #2684FC;
}

