.popup {
  background: rgba(0, 0, 0, 0.4);
  height: 100%;
  left: 50%;
  opacity: 0;
  overflow-y: auto;
  pointer-events: none;
  position: fixed;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  -webkit-transition: opacity 0.2s ease;
  -o-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease;
  width: 100%;
  z-index: 10000;
  display: none;
}

.popup.active {
  display: block;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__body {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  min-height: 100%;
  padding: 50px 15px 20px;
  width: 100%;
}

