  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.popup__content {
  position: relative;
  padding: 60px;
  border-radius: 30px;
  background-color: #FFFFFF;
  max-width: 920px;
  min-height: auto;
  scale: 0.4;
  -webkit-transition: scale 0.4s ease;
  -o-transition: scale 0.4s ease;
  transition: scale 0.4s ease;
  margin-left: auto;
  margin-right: auto;
}

.popup.open .popup__content {
  scale: 1;
  opacity: 1;
  transform: none;
}

.close-popup:not(.btn) {
  position: absolute;
  width: 24px;
  height: 24px;
  top: 30px;
  right: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  -webkit-transition: scale 0.2s ease, -webkit-transform 0.2s ease;
  transition: scale 0.2s ease, -webkit-transform 0.2s ease;
  -o-transition: transform 0.2s ease, scale 0.2s ease;
  transition: transform 0.2s ease, scale 0.2s ease;
  transition: transform 0.2s ease, scale 0.2s ease, -webkit-transform 0.2s ease;
  z-index: 10000;
}

